
import XHeader from 'vux/src/components/x-header/index.vue'

import QRCode from 'qrcodejs2'
export default {
components: {
XHeader,
},
data() {
return {
PositionValue: null,    //  当操作失败的时候  返回的提示信息
showPosition: false,   //  控制操作失败时弹窗显示或隐藏
allOver: false,    // 详情
user_id: null,   //  个人ID
info: '',

type_list: {},
getUser: '',   //  责任人头像
value: '',
typeList: [],
show: false,
section: '', //部门ID值
sectionName: '',
name: '',  // 姓名值
nameID: '',  // 责任人ID值
num: 0,        // 判断显示添加责任人还是未添加责任人

dutylist: [],
namelist: [],  // 人员列表
explain: "",  // 问题描述
way: "",  // 处理方式
suggest: "",  // 建议/补充
openDetails: false, //查看详情
come1: '',    // 原由类型
ids: null,
dizhi: 'https://demo.isaveu.cn/mobile/#/userEvaluation',

imgList: [],
imgcode: [],    //  通过请求后天之后返回的头像路径
limit:6, //限制图片上传的数量
options: {
getThumbBoundsFn (index) {
let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
},
}
},
mounted(){
this.ids = this.$route.query.id;
this.user_id = window.localStorage.getItem("user_id");
this.getInfo();
console.log(this.ids)
},
methods:{

clearOver(){
this.openDetails = false;
this.$router.push({
name:"call",
params:{
headValue: 1,
abnormal: 1
}
})
},

qrcode(){
let qrcode = new QRCode('qrcode', {
width: 120,
height:120,
text: this.dizhi,
})
console.log(qrcode._htOption.text)
},

gainbranch(){
console.log(this.section)
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getDepartmentUser,
type: "post",
data: {
department_id: this.section
},
success: (response, allResponse) => {
this.namelist = response
}
});
},

overSolve(){
this.allOver = !this.allOver;
},

getInfo(){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.reason,
type: "get",
data: {
id: this.ids,
},
success: (response, allResponse) => {
this.info = response;
this.type_list = response.waiter;
this.typeList = allResponse.abnormalTypeList
}
});
},

reptition1(){
console.log(this.come1)
},

back(){
this.$router.push({
name:"call",
params:{
headValue: 1,
abnormal: 1
}
})
},

onChange() {
console.log( this.value )
},

addDuty(){
this.show = true;
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getHumanUserDepartment,
type: "post",
data: {

},
success: (response, allResponse) => {
this.dutylist = response
}
});
},

onConfirm(){

this.dutylist.forEach((item,index) => {
if( item.key == this.section ){
this.sectionName = item.value
}
});
this.namelist.forEach((item,index) => {
if( item.key == this.name ){
this.name = item.value
this.nameID = item.key
}
});
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getUserInfo,
type: "post",
data: {
user_id: this.nameID
},
success: (response, allResponse) => {
this.getUser = response
}
});
this.num = 1;
if(this.section == null && this.name == null){
this.num = 0
}
},

magnfiy (index) {
this.$refs.previewer.show(index)
},

fileChange(e){
let file = e.target.files[0];
var reader = new FileReader();
reader.onload = (data) => {
let res = data.target || data.srcElement;
};
reader.readAsDataURL(file);
let param = new FormData(); 
param.append('file',file,file.name);
let config = {
headers:{'Content-Type':'multipart/form-data'}
};  //添加请求头
this.$ajax.post(this.common.apiUrl.servicecall,param,config)
.then(response=>{

this.imgList.push({
msrc: response.data.data.src,
src: response.data.data.src
});
this.imgcode.push(response.data.data.src);
console.log(this.imgcode);
})
},

delImg(index) {
this.imgList.splice(index, 1);
this.imgcode.splice(index, 1);
if (this.limit !== undefined) this.limit = 6-this.imgList.length;
},

submit(){
if(this.ids != null && this.nameID != '' && this.value != '' && this.come1 != '' && this.explain != '' && this.way != ''){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.reasonPOST,
type: "post",
data: {
id: this.ids,
duty_id: this.nameID,
type_id: this.value,
abnormal_status: this.come1,
desc: this.explain,
abnormal_res: this.way,
add_remark: this.suggest,
img: this.imgcode
},
success: (response, allResponse) => {
this.showPosition = true;
this.PositionValue = allResponse.msg;
this.openDetails = true;
this.dizhi = this.dizhi + '?shopId=' + this.info.shop_id + '&id=' + this.ids + '&waiterId=' + this.user_id;
this.qrcode();
}
});
}else{
this.showPosition = true;
this.PositionValue = '您还有数据没填写~'
}
},
},

beforeDestroy(){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.reason_back,
type: "post",
data: {
id: this.ids,
},
success: (response, allResponse) => {

}
});
}
}



